.footable.table-with-table-inner {
  & > tbody {
    td {
      & > div {
        & > * {
          display: flex; }

        a {
          color: $accent; }

        .accent {
          color: #B4BACA; } } } } }

.footable.table-with-table-inner {
  tr[data-expanded="true"] {
    background: #F8F9FA; } }

table.footable.breakpoint.table-with-table-inner {
  td.footable-last-visible {
    padding-right: 70px; }
  .footable-toggle {
    right: 15px; } }

.footable-details.table-with-table-inner {
  tr {
    th {
      display: none; }
    td {
      padding: 0!important;
      @include r(767) {
        padding: 10px!important;
        box-sizing: border-box; }

      .button-group {
        flex-direction: row; } }


    div[data-label] {

      @include r(767) {
        display: flex;
        justify-content: space-between;
        align-items: center;
        &::before {
          content: attr(data-label);
          width: 45%;
          text-align: left;
          font-size: 12px;
          @include line(1); }

        & > div {
          width: 45%;
          & > * {
            @include line(1); } } } } } }


.table-inner {
  display: block;
  .table-inner__row {
    display: grid;
    grid-template-columns: repeat(3, 33%);
    text-align: left;
    justify-content: space-between;
    align-items: center;
    @include r(767) {
      display: flex;
      flex-direction: column;
      align-items: stretch; }
    & + .table-inner__row {
      border-top: 1px solid #fff; }
    .table-inner__cell {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 15px;
      @include r(1199) {
        padding: 10px; }
      @include r(767) {
        padding: 10px 0; }
      > div {
        & + div {
          margin-left: 20px; }
        & > * {
          br {
            @include r(1399) {
              display: none; } } } }
      & > div {
        @include r(767) {
          width: 50%;
          &:last-child {
            text-align: right; }

          .button-group {
            @include r(767) {
              justify-content: flex-end; } } }

        & > * {
          display: flex; }


        a {
          color: $accent; }

        .accent {
          color: #B4BACA; } }
      a {
        white-space: normal!important;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 130%;
        letter-spacing: 0.02em;
        color: #30393E;
        @include r(1199) {
          font-size: 12px; } }
      strong {
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 100%;
        letter-spacing: 0.02em;
        color: #30393E;
        white-space: nowrap;
        margin-left: 20px;
        @include r(1199) {
          font-size: 14px;
          margin-left: 10px; } } } } }

.table-inner__product {
  display: flex;
  align-items: center;

  .table-inner__product-img {
    width: 90px;
    height: 55px;
    flex: 0 0 90px;
    margin-right: 15px;
    img {
      max-width: 100%;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center; } }

  .table-inner__product-txt {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 130%;
    letter-spacing: 0.02em;
    color: #30393E;
    word-break: normal;
    @include r(1199) {
      font-size: 12px; } } }

.table-before-btn {
  display: block;
  position: relative;
  & > * {
    position: absolute!important;
    top: 10px;
    right: 30px;
    z-index: 1;
    @include r(1499) {
      right: 20px; }
    @include r(1199) {
      right: 5px; }
    @include r(767) {
      right: 10px; } }

  &.--custome {
    & > * {
      top: 30px; } }

  & > .button.ico_trash {
    background: #FFEFF1;
    color: #FF5F72;
    &::before {
      color: #FF5F72; } }

  & + table {
    tr {
      th.footable-first-visible {
