          text-align: left!important; } }
      &.footable-detail-row {
        th, td {
          text-align: left!important;
          &:last-child {
            padding-right: 5px; }

          &.text-end {
            text-align: right!important;
            .button-group {
              justify-content: flex-end; } }
          .js-more {
            text-align: left; } } } } }


  &.--small {
    & + table {
      tr {
        th.footable-last-visible {
          padding-right: 70px!important; }
        th, td {
          &:last-child {
            min-width: 70px; } } } } }

  &.--small {
    & > * {
      right: 15px;
      @include r(1199) {
        right: 5px; }
      @include r(767) {
        right: 10px; } }
    & + table.table-td-small {
      tr {
        th.footable-last-visible {
          padding-right: 35px!important; } }
      tr th:last-child,
      tr td:last-child {
        min-width: 65px;
        padding-right: 15px; } } } }


.table-nav .footable-pagination-wrapper .footable-page-nav[data-page="next"].disabled {
