.page-index {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  background: #25282B;
  overflow: hidden;
  padding: 40px 0 50px; }

.login-form-box {
  display: block;
  position: relative;
  width: 570px;

  @include r(767) {
    width: 290px; }

  &::before {
    content: '';
    display: block;
    position: absolute;
    left: -150px;
    top: 50%;
    transform: translateY(-50%);
    width: 220px;
    height: 790px;
    background-image: url(../img/decor_form-box.svg);
    background-repeat: no-repeat;
    background-position: center; } }

.login-form-wrapper {
  opacity: 0;
  animation-name: opacity;
  animation-duration: 500ms;
  animation-fill-mode: forwards;
  animation-timing-function: linear;
  animation-delay: 3500ms; }

.login-form-logo {
  padding-bottom: 50px;
  margin-bottom: 60px;
  text-align: center;
  position: relative;

  @include r(767) {
    margin-bottom: 20px; }

  &::before {
    content: '';
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 35px;
    background-image: url(../img/decor_login-form-logo.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain; }

  h4 {
    font-style: normal;
    font-weight: 500;
    font-size: 10px;
    line-height: 12px;
    letter-spacing: 0.3em;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.5); } }

.login-copy {
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  text-align: center;

  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  letter-spacing: 0.02em;
  color: #C3C8D5;

  opacity: 0;
  animation-name: opacity;
  animation-duration: 500ms;
  animation-fill-mode: forwards;
  animation-timing-function: linear;
  animation-delay: 3700ms;

  @include r(767) {
    margin-top: 70px; }

  a {
    color: $yellow; }

  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-image: url(../img/logo-fairtech.svg);
    background-repeat: no-repeat;
    background-position: center;
    width: 100px;
    height: 100px; } }


.login-form {
  padding: 90px 100px;
  position: relative;

  @include r(767) {
    padding: 30px; }

  .border {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 100%;
    height: 100%;
    border: 0!important;
    pointer-events: none;

    > span {
      display: block;
      position: absolute;
      background-color: #FFC039; }

    span {
      &.right-top {
        -webkit-transition-property: height;
        -o-transition-property: height;
        -webkit-animation-name: right-top;
        -webkit-animation-duration: 500ms;
        -webkit-animation-delay: 500ms;
        -webkit-animation-timing-function: linear;
        -webkit-animation-fill-mode: forwards;
        right: 0;
        bottom: calc(100% - 44px);
        width: 8px;
        height: 0px;
        animation-name: right-top;
        animation-duration: 500ms;
        animation-fill-mode: forwards;
        animation-timing-function: linear;
        animation-delay: 500ms;
        transition-property: height; }

      &.top {
        -webkit-transform: translateX(-100%);
        -ms-transform: translateX(-100%);
        -webkit-transform-origin: right;
        -ms-transform-origin: right;
        -webkit-animation-name: top;
        -webkit-animation-duration: 1000ms;
        -webkit-animation-timing-function: linear;
        -webkit-animation-delay: 1000ms;
        -webkit-animation-fill-mode: forwards;
        top: 0;
        left: 100%;
        width: 0;
        height: 8px;
        transform: translateX(-100%);
        transform-origin: right;
        animation-name: top;
        animation-duration: 1000ms;
        animation-fill-mode: forwards;
        animation-timing-function: linear;
        animation-delay: 1000ms; }

      &.left {
        -webkit-animation-name: left;
        -webkit-animation-duration: 500ms;
        -webkit-animation-timing-function: linear;
        -webkit-animation-delay: 2000ms;
        -webkit-animation-fill-mode: forwards;
        top: 0;
        left: 0;
        width: 8px;
        height: 0;
        animation-name: left;
        animation-duration: 500ms;
        animation-fill-mode: forwards;
        animation-timing-function: linear;
        animation-delay: 2000ms; }

      &.bottom {
        -webkit-animation-name: bottom;
        -webkit-animation-duration: 1000ms;
        -webkit-animation-timing-function: linear;
        -webkit-animation-delay: 2500ms;
        -webkit-animation-fill-mode: forwards;
        bottom: 0;
        left: 0;
        width: 0;
        height: 8px;
        animation-name: bottom;
        animation-duration: 1000ms;
        animation-fill-mode: forwards;
        animation-timing-function: linear;
        animation-delay: 2500ms; }

      &.right-bottom {
        -webkit-animation-name: right-bottom;
        -webkit-animation-duration: 500ms;
        -webkit-animation-delay: 3500ms;
        -webkit-animation-timing-function: linear;
        -webkit-animation-fill-mode: forwards;
        right: 0;
        bottom: 0;
        width: 8px;
        height: 0;
        animation-name: right-bottom;
        animation-duration: 500ms;
        animation-fill-mode: forwards;
        animation-timing-function: linear;
        animation-delay: 3500ms; } } } }

.login-form {

  .decor {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 100%;
    height: 100%;
    pointer-events: none;

    &::before, &::after {
      display: block;
      position: absolute;
      background-repeat: no-repeat;
      content: ''; }

    &::before {
      top: -10px;
      right: -7px;
      width: 162px;
      height: 67px;
      background-image: url(../img/decor_form-right.svg);
      opacity: 0;
      animation-name: opacity;
      animation-duration: 500ms;
      animation-fill-mode: forwards;
      animation-timing-function: linear;
      animation-delay: 1000ms; }

    &::after {
      bottom: -10px;
      left: -7px;
      width: 215px;
      height: 215px;
      background-image: url(../img/decor_form-left.svg);
      opacity: 0;
      animation-name: opacity;
      animation-duration: 500ms;
      animation-fill-mode: forwards;
      animation-timing-function: linear;
      animation-delay: 2500ms; } } }

@-webkit-keyframes right-top {
  0% {
    height: 0; }

  100% {
    height: 44px; } }

@keyframes right-top {
  0% {
    height: 0; }

  100% {
    height: 44px; } }

@-webkit-keyframes top {
  0% {
    width: 0; }

  100% {
    width: 100%; } }

@keyframes top {
  0% {
    width: 0; }

  100% {
    width: 100%; } }

@-webkit-keyframes left {
  0% {
    height: 0; }

  100% {
    height: 100%; } }

@keyframes left {
  0% {
    height: 0; }

  100% {
    height: 100%; } }

@-webkit-keyframes bottom {
  0% {
    width: 0; }

  100% {
    width: 100%; } }

@keyframes bottom {
  0% {
    width: 0; }

  100% {
    width: 100%; } }

@-webkit-keyframes right-bottom {
  0% {
    height: 0; }

  100% {
    height: 44px; } }

@keyframes right-bottom {
  0% {
    height: 0; }

  100% {
    height: 44px; } }

@keyframes opacity {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.login-form {

  .form-group {
    span {
      display: inline-block;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 17px;
      color: #B4BACA;
      cursor: pointer;
      @include tr(0.25s);
      @include h {
        color: $yellow; } } }

  .button {
    height: 50px; }

  .form-control {
    background: none;
    border: 2px solid #5A5D65;
    box-sizing: border-box;
    border-radius: 6px;
    height: 50px;

    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;

    @include placeholder() {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 17px;
      color: #B4BACA; }

    &:hover,
    &:focus {
      border: 2px solid #FFDE3F;
      box-sizing: border-box;
      border-radius: 6px; } } }



#m-phone-country {
  .modal-content {
    background: none;
    box-shadow: none;
    border: none;
    margin-top: -100px; }

  .modal-title {
    color: #fff;
    margin-bottom: 30px; }

  .modal-body {
    max-height: 380px;
    overflow: auto;
    scrollbar-color: #5A5D65 #1D2022;
    scrollbar-width: thin!important;
    -moz-appearance: none !important;
    padding-right: 20px;
    &::-webkit-scrollbar {
      width: 4px; }
    &::-webkit-scrollbar-track {
      background-color: #1D2022; }
    &::-webkit-scrollbar-thumb {
      background-color: #5A5D65; } } }

ul.coutry-number-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  li {
    display: block;

    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
    padding: 5px 15px;
    @include tr(0.25s);

    @include h {
      background: #1D2022;

      .check__box::after {
        opacity: 1; } }

    label.radio {
      display: block;
      position: relative;
      width: 100%; }

    .check__box::before {
      background: none;
      border-radius: 50%; }

    .check__box::after {
      background: none;
      width: 9px;
      height: 9px;
      background: $yellow;
      border-radius: 50%;
      left: 8px; }

    .check__input:checked + .check__box:before {
      border-color: $yellow; }

    .check__box {
      display: flex;
      justify-content: space-between;
      align-items: center;

      span:last-child {
        margin-left: auto; } } } }


.form-group.--phone-group {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border: 2px solid #5A5D65;
  height: 50px;
  border-radius: 6px;
  padding: 0 15px;

  &:hover {
    border: 2px solid $yellow;
    a {
      color: #FFFFFF!important;
      @include ico() {
        color: $yellow; } } }

  a {
    display: flex!important;
    justify-content: flex-start;
    align-items: center;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    color: #FFFFFF;
    margin-right: 15px;
    padding-right: 15px;
    border-right: 1px solid #5A5D65;
    cursor: pointer;
    text-decoration: none;
    @include ico() {
      margin-left: 5px; }

    @include r(767) {
      margin-right: 7px;
      padding-right: 7px; } }

  input {
    background: none;
    border: none;
    color: #FFFFFF;
    font-size: 14px;
    font-style: normal;
    font-weight: normal;
    line-height: 1.21; } }

.login-form-toggle {
  display: block;
  position: relative;
  min-height: 225px; }

.login-form-off {
  display: none;

  h4 {
    color: #fff;
    text-align: center;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px; } }

#m-phone-success {
  .modal-content {
    background: none;
    box-shadow: none;
    border: none;
    margin-top: -100px; }
  .modal-body {
    text-align: center;
    img {
      margin-bottom: 30px; }
    h4 {
      color: #fff;
      font-style: normal;
      font-weight: 500;
      font-size: 18px;
      line-height: 21px; } } }


.login-form-blocked {
  display: none;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  text-align: center;
  margin-top: 20px;
  img {
    margin-bottom: 15px; }
  h1, h2, h3, h4, h5, h6 {
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 120%;
    color: #FFFFFF; } }

body.page-is-blocked {
  .login-form-on,
  .login-form-off {
    display: none; }
  .login-form-blocked {
    display: flex; } }


button.blocked {
  display: flex;
  justify-content: center;
  align-items: center;
  background: $yellow;
  width: 50px;
  height: 50px;
  flex: 0 0 50px;
  border: 0;
  border-radius: 8px;
  position: fixed;
  left: 20px;
  bottom: 20px;
  &.is-active {
    background: $accent;
    color: #fff; } }
