.form-group {
  margin-bottom: 20px; }

.form-control {
  display: inline-block;
  height: 45px;
  line-height: 42px;
  padding: 0 15px;
  background: #FFFFFF;
  border: 1.5px solid #E1E3EA;
  box-sizing: border-box;
  border-radius: 6px;

  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  color: #000;
  @include fz(14, 12);

  &:focus {
    border: 1.5px solid #B4BACA;
    @include placeholder() {
      opacity: 0; } }


  @include placeholder() {
    color: #C3C8D5;
    text-overflow: ellipsis;
    opacity: 1;
    transition: opacity 0.3s ease;
    @include r(767) {
      font-size: 12px; } } }

textarea.form-control {
  height: 90px;
  line-height: 1.3;
  padding-top: 15px;
  resize: none; }

.input-group-text {
  display: flex;
  justify-content: center;
  align-items: center;
  background: none;
  border: 1.5px solid #E1E3EA;
  border-right: 0;
  min-width: 60px;

  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #C3C8D5;

  @include fz(14, 12); }

.nav-pills .nav-item {
  margin-right: 10px;
  &:last-child {
    margin-right: 0; } }

.nav-pills .nav-link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 15px;
  border: 2px solid #E1E3EA;
  box-sizing: border-box;
  border-radius: 30px;
  height: 30px;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 1.1;
  color: #30393E; }

.nav-pills .nav-link.active,
.nav-pills .show>.nav-link {
  border: 2px solid $yellow;
  color: #30393E;
  background: none; }

.form-control.is-invalid,
.was-validated .form-control:invalid, {
  background: none;
  border: 1px solid #FF5F72; }

.form-control.is-valid,
.was-validated .form-control:valid {
  background: none;
  border-color: #5A5D65; }

.invalid-feedback {
  width: auto;
  height: 20px;
  position: absolute;
  top: -10px;
  right: -10px;
  background: #FF5F72;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: 0.01em;
  color: #FFFFFF;
  padding: 0 10px;
  margin: 0; }
