.search {
  display: block;
  position: relative;
  margin-bottom: 20px;
  &.is-active {
    z-index: 1001;
    .search__label {
      background: #B4BACA;
      @include h {
        background: $yellow;
        cursor: pointer; } }
    @include r(767) {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      overflow-y: auto;
      background-color: rgba(#000, 0.65);
      padding: 0 15px 30px;
      box-sizing: border-box;
      .search__head {
        display: flex; }
      .search__footer {
        display: block; } } }

  &__head {
    display: none;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    margin-bottom: 20px; }
  &__title {
    display: flex;
    align-items: center;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 1.25;
    color: #000000; }
  &__close {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: $accent;
    color: #fff;
    cursor: pointer; }
  &__body {
    display: flex;
    position: relative; }
  &__footer {
    margin-top: 30px;
    display: none;
    .uk-button {
      width: 100%;
      &+ .uk-button {
        margin-top: 10px; } } }

  &__label {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 45px;
    height: 45px;
    background: #E1E3EA;
    border-radius: 6px 0px 0px 6px;
    padding: 0 10px;
    box-sizing: border-box;
    color: #fff; }
  &__controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 45px;
    background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle fill='none' stroke='%23DCDCDC' stroke-width='1.1' cx='9' cy='9' r='7'%3E%3C/circle%3E%3Cpath fill='none' stroke='%23DCDCDC' stroke-width='1.1' d='M14,14 L18,18 L14,14 Z'%3E%3C/path%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-position: right 10px center;
    background: #FFFFFF;
    border: 1px solid #F2F2F2;
    box-sizing: border-box;
    border-radius: 0px 6px 6px 0px;
    padding-right: 50px;
    padding-left: 10px;

    @include placeholder() {
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden; }

    input {
      border: 0;
      width: 100%;
      height: 45px;
      line-height: 45px;
      padding-left: 15px;
      outline: 0;
      background-color: transparent;
      margin-right: 10px;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      color: #30393E;
      text-overflow: ellipsis;
      @include r(767) {
        box-sizing: border-box;
        margin: 0;
        padding: 0;
        height: 30px;
        line-height: 30px; }
      @include placeholder() {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        color: #C3C8D5; } } }

  &__result {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    opacity: 0;
    pointer-events: none;
    @include tr(0.5s);
    @include r(767) {
      display: none!important; }
    &.is-show {
      opacity: 1;
      pointer-events: all;
      @include r(767) {
        display: flex;
        margin-top: 10px; } } }

  &__drop {
    display: none;
    position: absolute;
    top: calc(100% + 5px);
    left: 45px;
    padding: 20px 15px;
    width: calc(100% - 45px);
    box-sizing: border-box;
    background-color: #fff;
    border: 1px solid #F2F2F2;
    box-sizing: border-box;
    border-radius: 6px;
    z-index: 2;
    @include r(1199) {
      left: 0;
      width: 100%; } }

  &__items {
    table {
      margin: 0; } }

  &__btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 20px;
    @include r(767) {
      flex-direction: column-reverse; }
    & > * {
      width: 30%;
      @include r(1199) {
        width: auto; }
      &:first-child {
        @include r(1199) {
          display: none; } } }
    .table-nav {
      @include r(767) {
        margin-bottom: 20px; }
      .drop {
        min-width: 100px; } } } }

.result-list {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  list-style-type: none;
  padding: 0;
  margin: 0;
  li {
    @include r(767) {
      margin-right: 12px;
      margin-bottom: 10px; }
    & + li {
      margin-left: 5px;
      @include r(767) {
        margin-left: 0; } } } }

.result-item {
  display: flex;
  align-items: center;
  background: #FFEFF1;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 0 8px;
  height: 22px;
  &__title {
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 100%;
    align-items: center;
    letter-spacing: 0.02em;
    color: #30393E; }
  &__del {
    margin-left: 3px;
    font-size: 10px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #FF5F72;
    @include tr(0.3s);
    @include h {
      color: red; } } }

.result-clear {
  background: #FF5F72;
  box-sizing: border-box;
  border-radius: 4px;
  padding: 0 8px;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 22px;
  letter-spacing: 0.02em;
  color: #FFFFFF;
  margin-left: 5px;
  cursor: pointer;
  white-space: nowrap;
  opacity: 0;
  pointer-events: none;
  @include tr(0.3s);
  @include r(767) {
    margin-left: 0; }
  @include h {
    background-color: #FF5F72;
    color: #fff; }
  &.is-show {
    opacity: 1;
    pointer-events: all; } }


.search__overlay {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(90, 93, 101, 0.6);
  z-index: -1;
  opacity: 0;
  pointer-events: none;
  @include tr(0.5s);
  &.is-show {
    opacity: 1;
    z-index: 1000; } }


.block-editor {
  display: block;
  position: relative;
  border: 1.5px solid #E1E3EA;
  box-sizing: border-box;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 20px;
  &:focus {
    border: 1.5px solid #B4BACA; }

  .block-editor-head {
    background: #f2f2f2; }
  .block-editor-body {
    padding: 30px;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 140%;
    letter-spacing: 0.02em;
    color: #30393E;
    @include r(767) {
      padding: 10px 10px 15px; }
    a {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 1.71;
      text-decoration-line: underline;
      color: $accent;
      @include tr(0.3s);
      @include h {
        text-decoration: none; } }

    ul, ol {
      li {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 1.71;
        color: $accent; } }

    .align-center {
      text-align: center; }

    .accent-text {
      font-weight: bold;
      font-size: 18px;
      @include fz(18, 14);
      line-height: 1.83;
      text-align: center;
      color: #236FA1; } } }

.block-editor-save {
  .uk-button {
    padding: 0 50px; } }
