.list-info {
  list-style-type: none;
  padding: 0;
  margin: 0 0 20px 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  li {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    & + li {
      margin-top: 10px; }
    span {
      display: block;
      position: relative;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 14px;
      letter-spacing: 0.02em;
      color: #B4BACA;
      margin-bottom: 5px; }
    strong {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 100%;
      letter-spacing: 0.02em;
      color: #30393E; }
    a {
      display: block;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 100%;
      letter-spacing: 0.02em;
      color: #628EFF;
      max-width: 200px;
      @include line(1); }

    span.value {
      display: flex;
      align-items: center;
      & > * + * {
        margin-left: 10px;
        padding-left: 10px;
        border-left: 1px solid #E1E3EA; } } } }

.more-content {
  margin: 0;
  @include tr(0.25s);
  & ~ .more-hide {
    display: inline-block; }
  & ~ .more-show {
    display: none; }
  &.hide {
    @include line(2);
    & ~ .more-show {
      display: inline-block; }
    & ~ .more-hide {
      display: none; } } }

span.more-show,
span.more-hide {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 120%;
  letter-spacing: 0.02em;
  color: #628EFF;
  cursor: pointer;
  @include tr(0.25s);
  @include h {
    color: $yellow; } }
