.product-table {
  thead {
    th {
      padding: 10px 30px;
      @include r(1499) {
        padding: 12px 20px; } } }
  tbody {
    tr {
      td {
        font-size: 12px;
        padding: 10px 30px;
        @include r(1499) {
          padding: 12px 20px; }
        &:first-child {
          padding-right: 5px; }
        &:nth-child(2) {
          padding-left: 0;
          padding-right: 10px;
          &::before {
            display: none; } } } } }

  .box-product {
    display: flex;
    align-items: center;
    img {
      min-width: 80px;
      margin-right: 15px;
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 120%;
      letter-spacing: 0.01em;
      color: #30393E;
      @include r(374) {
        min-width: 60px;
        max-width: 60px; } }
    a {
      font-size: 12px;
      @include r(374) {
        font-size: 10px; } } } }
