body {
  min-height: 100vh;
  font-size: 16px;
  @include r(767) {
    min-height: auto;
    font-size: 12px; } }

.page-wrapper {
  min-width: 320px;
  max-width: 1920px;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  overflow: auto;
  scrollbar-color: #C3C8D5 #F0F1F4;
  scrollbar-width: thin!important;
  -moz-appearance: none !important;
  &::-webkit-scrollbar {
    width: 4px; }
  &::-webkit-scrollbar-track {
    background-color: #F0F1F4; }
  &::-webkit-scrollbar-thumb {
    background-color: #C3C8D5; }
  @include r(767) {
    display: block;
    min-height: auto;
    overflow: hidden; }

  .page-head {
    display: flex;
    justify-content: center;
    flex-direction: column;
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      @include r(767) {
        flex-direction: column;
        align-items: stretch; } }
    &__logo {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      width: 300px;
      height: 100px;
      background: #25282B;
      @include r(767) {
        width: 100%; }
      h4 {
        font-style: normal;
        font-weight: 500;
        font-size: 10px;
        line-height: 12px;
        text-align: center;
        letter-spacing: 0.3em;
        text-transform: uppercase;
        color: rgba(255, 255, 255, 0.5); } }
    &__content {
      display: flex;
      align-items: center;
      flex-grow: 1;
      height: 70px;
      background: #F8F9FA;
      padding-left: 75px;
      padding-right: 75px;
      @include r(1499) {
        padding-left: 30px;
        padding-right: 30px; }
      @include r(1359) {
        padding: 0 20px;
        height: 100px; }
      @include r(767) {
        width: 100%; }
      @include r(575) {
        padding: 10px 15px;
        height: auto; }
      .container-large {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        @include r(575) {
          flex-direction: column; } }
      h1, h2, h3, h4, h5, h6 {
        font-weight: 500;
        font-size: 18px;
        line-height: 21px;
        color: #30393E;
        margin: 0; } } }

  ul.lang-list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @include r(575) {
      margin-top: 10px; }
    li {
      display: block;
      position: relative;
      & + li {
        margin-left: 7px; }
      a {
        display: inline-flex;
        align-items: center;
        position: relative;
        padding: 10px 15px;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 100%;
        letter-spacing: 0.04em;
        color: #30393E;
        border-radius: 6px;
        @include tr(0.3s);
        @include r(1359) {
          padding: 10px; }
        img {
          margin-right: 10px; }
        &.is-active,
        &:hover,
        &:focus {
          background: #E1E3EA; } } } }

  .page-content {
    flex-grow: 1;
    display: flex;
    @include r(1359) {
      flex-direction: column; } }

  .page-main {
    flex-grow: 1;
    padding: 0 80px 80px;
    margin: 15px 0 0;
    box-sizing: border-box;
    @include r(1499) {
      padding: 0 30px 30px;
      margin: 0; }
    @include r(1359) {
      flex: 0 0 calc(100% - 220px); }
    @include r(1199) {
      padding: 0 15px 30px; }
    .container-content {
      max-width: 1330px; } }

  .page-content-head {
    @include r(1359) {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 30px 0 20px;

      @include r(767) {
        flex-direction: column;
        align-items: stretch;
        justify-content: stretch;
        margin-top: 0; }

      &::after {
        content: '';
        display: block;
        width: 33%;
        @include r(767) {
          display: none; } } }

    & > * {
      @include r(1199) {
        width: 33%; }
      @include r(767) {
        width: 100%; } }

    h4 {
      @include r(1199) {
        text-align: center;
        margin: 0; } } } }

.page-back {
  display: flex;
  align-items: center;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
