.datepicker--cell.-current- {}
.datepicker--cell.-current-.-selected- {
  background: none;
  color: #30393E; }

.datepicker--cell.-selected- {
  color: #30393E;
  background: none;

  &::before {
    content: '';
    display: block;
    width: 30px;
    height: 30px;
    background-color: $yellow;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
