.datepicker--cell.-in-range-, {
  background: rgba(#FFDE3F, 0.3); }

.datepicker--cell.-selected-.-range-from- {
  background: rgba(#FFDE3F, 0.3);
  border-radius: 15px 0 0 15px; }

.datepicker--cell.-selected-.-range-to- {
  background: rgba(#FFDE3F, 0.3);
