.dropify-wrapper {
  width: auto;
  height: 100px;
  background: #FFFFFF;
  box-shadow: 8px 8px 50px 3px #F0F1F4;
  border-radius: 20px;
  padding: 0;
  border: none;
  overflow: visible;
  position: relative;

  .dropify-errors-container {
    display: none; }

  .dropify-error {
    font-size: 10px;
    line-height: 1; }

  &.touch-fallback {
    min-height: 100px !important;

    .dropify-clear {
      top: -5px;
      color: #fff; }

    .dropify-preview {
      position: absolute;
      .dropify-render {
        position: absolute;
        img {
          transform: translateY(-50%); } }
      .dropify-infos {
        position: absolute;
        opacity: 0; } }

    .dropify-message {
 } }      // transform: translateY(-50%)

  &.has-preview {
    .dropify-clear {
      display: flex;
      opacity: 1; }

    @include h {
      .dropify-message {
        display: none; } } }

  .dropify-clear {
    display: none;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    background: #FA8A96;
    border-radius: 50%;
    border: none;
    padding: 0;
    @include icon(close);
    top: -5px;
    right: -5px;
    &::before {
      font-size: 8px;
      position: relative; }
    @include h {
      background-color: #f00; } }

  .dropify-message {
    padding: 27px 0!important;
    top: calc(50% + 5px);
    z-index: 5;
    p.dropify-error {
      display: none; } }

  .dropify-preview {
    padding: 0;
    border-radius: 20px;
    overflow: hidden;

    .dropify-render {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      img {
        min-width: 100%;
        min-height: 100%;
        object-fit: cover; } }

    .dropify-infos {
      .dropify-infos-inner {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 10px;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transform: none;
        margin-top: 0!important;

        p {
          font-size: 11px;
          line-height: 1;
          white-space: normal!important;
          text-overflow: none!important;
          overflow: visible!important; } }

      .dropify-filename {
        font-size: 11px;

        .file-icon {
          display: none; }

        .dropify-filename-inner {
          @include line(3); } }

      .dropify-infos-message {
        display: none; } } } }

