.nav-tabs {
  border: none;
  display: inline-flex;
  position: relative;
  @include r(1199) {
    display: flex;
    justify-content: center; }
  @include r(767) {
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch; }
  &::after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 4px;
    background: linear-gradient(0deg, #F0F1F4, #F0F1F4), #628EFF;
    @include r(767) {
      width: 4px;
      height: 100%;
      top: 0;
      left: 0; } } }

.nav-tabs .nav-item.show .nav-link,
.nav-tabs .nav-link.active {
