.users-type-action {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  .drop {
    &-input {
      height: 35px; } } }

.users-table {
  max-width: 1170px;
  thead {
    th {
      padding: 10px 20px;
      @include r(1399) {
        padding: 10px; } } }
  tbody {
    tr {
      td {
        font-size: 12px;
        padding: 10px 20px;
        word-break: break-all;
        @include r(1399) {
          padding: 10px; }
        & > * {
          word-break: break-all; } } }

    &.tb-sortable {
      tr {
        &.ui-sortable-helper {
          max-width: 1170px;
          td:nth-child(2) {
            width: 100%; } }
        td:first-child {
          color: $accent;
          cursor: grab; } } } } }

table.users-table tr th:last-child,
table.users-table tr td:last-child {
  min-width: auto; }

.table-before-btn + table.users-table tr th.footable-last-visible {
  padding-right: 65px !important; }

.user-info {
  background: #F8F9FA;
  padding: 30px 0 30px 30px;

  .scroll-box {
    max-height: 190px;
    overflow: auto; } }


.list-info-title {
  display: block;
  position: relatives;
  margin-bottom: 5px;

  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 100%;
  display: flex;
  align-items: center;
  letter-spacing: 0.01em;
  color: #B4BACA; }

.id-adress {
  cursor: pointer;
  &.--locked {
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    color: #FF5F72;
    @include icon(locked);
    &::before {
      display: inline-block;
      width: 18px;
      height: 18px;
      flex: 0 0 18px;
      margin-top: -3px;
      margin-left: 12px;
      font-size: 18px;
      color: #FF5F72; } }
  &.--unlocked {
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    color: #57CA70;
    @include icon(unlocked);
    &::before {
      display: inline-block;
      width: 18px;
      height: 18px;
      flex: 0 0 18px;
      margin-top: -3px;
      margin-left: 12px;
      font-size: 18px;
      color: #57CA70;
      @include tr(0.25s); } } }
