.option-item {
  display: block;
  position: relative;
  background: #F8F9FA;
  padding: 20px;
  &__box {
    display: flex;
    justify-content: space-between;
    align-items: flex-start; }
  &__color {
    width: 90px;
    height: 90px;
    flex: 0 0 90px;
    border: 6px solid #FFFFFF;
    box-sizing: border-box;
    border-radius: 6px;
    @include r(767) {
      width: 60px;
      height: 60px;
      flex: 0 0 60px; } }
  &__list {
    flex-grow: 1;
    margin: 0 15px;
    align-self: center;
    ul {
      list-style: none;
      padding: 0;
      margin: 0;
      li {
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;
        color: #30393E;
        @include fz(14, 12); } } }
  &__action {
    .action-group-btn {
      background: #F0F1F4; } } }
